

export default function Cover({ children, image, height, style, imageStyle }) {
    return (
        <div className={"relative " + style}>
            <div className={"z-10 " + style}>
                {children}
            </div>
            <div className="z-0 absolute top-0 left-0 w-full h-full overflow-hidden ">
                <img className={`${height} w-full object-cover object-center ${imageStyle}`} src={image} alt="image cover" />
            </div>
        </div>
    )
}

export function Cover1({ children, image, height, style, imageStyle }) {
    return (
        <div className="relative flex overflow-hidden my-16 mx-16 bg-gray-900">
            <img className={`object-cover object-center ${imageStyle}`} src={image} alt="image cover" />
            <div className={"absolute flex justify-center items-center bg-red-500 opacity-75 w-full h-full"}>
                <p>click here</p>
            </div>
        </div>
    )
}